<template>
  <div class="container">
    <h1>Skriv något kul</h1>
    <div class="create-post">
      <input type="text" v-model="text">
      <button class="add-task-button" v-on:click="createPost">Add</button>
    </div>
    <!-- Create post -->
    <p class="error" v-if="error">{{error}}</p>
    <div class="posts-container">
      <div class="post"
        v-for="(post, index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post._id"
      >

      <!-- Edit post -->
      <p class="text"
        v-if ="!post.editmode"
        v-on:click="enterEditmode(post)"
        v-bind:class="{finishedTask: post.isFinished}"
        >
        {{post.text}}
      </p>
      <input class="input-field"
        v-else
        v-model="post.text"
        >
        <p class="text-date">Added  {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}</p>
      <!-- Delete / Update buttons -->
        <button class="delete-button"
        v-on:click="deletePost(post._id)">Delete</button>
      <button class="update-button"
        v-on:click="updatePost(post._id, post.text)">Update</button>
        <p>Klicka på texten för att ändra</p>
    </div>
  </div>
  </div>
</template>

<script>
import PostService from '../PostService';
export default {
  name: 'PostComponent',
data(){
  return{
    posts: [],
    error: '',
    text: ''
  }
},
async created(){
  try {
    this.posts = await PostService.getPosts();

    }catch(err){
      this.error = err.message;
  }
},
methods: {
  async createPost(){
    await PostService.insertPost(this.text);
    this.posts = await PostService.getPosts();
  },
  async deletePost(id){
    await PostService.deletePost(id);
    this.posts = await PostService.getPosts();
  },
async updatePost(id, text) {
      await PostService.updatePost(id, text);
      this.posts = await PostService.getPosts();
    },
    enterEditmode(post) {
      post.editmode = true;
      this.posts = [...this.posts];
    },
  },
}


</script>

