div.post { position: relative; border: 1px solid #000000; background-color: #e2b0b0; padding: 10px 10px 30px 10px; margin-bottom: 15px; }

div.created-at { position: absolute; top: 0; left: 0; padding: 5px 15px 5px 15px; background-color: darkgreen; }

p.text { font-size: 22px; font-weight: 700; margin-bottom: 0; }

input[type=text] {
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  background-color: #3CBC8D;
  color: white;
  transition: width 1.4s ease-in-out;
}

input[type=text]:focus {
  width: 100%;
}



</style>
